function formatToLongString(time, words) {
  if (time > 1) {
    return ` ${time} ${words[1]}`
  }
  return ` ${time} ${words[0]}`
}

export default {
  short: {
    now: '',
    sec: '',
    min: '',
    hour: '',
    day: '',
  },
  long: {
    now: '',
    sec: (time) => formatToLongString(time, ['', '']),
    min: (time) => formatToLongString(time, ['', '']),
    hour: (time) => formatToLongString(time, ['', '']),
    day: (time) => formatToLongString(time, ['', '']),
  },
}
