function formatToLongString(time, words) {
  let _time = time % 100

  if (_time > 19) {
    _time = _time % 10
  }

  switch (_time) {
    case 1: {
      return ` ${time} ${words[0]} `
    }
    case 2:
    case 3:
    case 4: {
      return ` ${time} ${words[1]} `
    }
    default: {
      return ` ${time} ${words[2]} `
    }
  }
}

export default {
  short: {
    now: ' ',
    sec: '',
    min: '',
    hour: '',
    day: '',
  },
  long: {
    now: ' ',
    sec: (time) => formatToLongString(time, ['', '', '']),
    min: (time) => formatToLongString(time, ['', '', '']),
    hour: (time) => formatToLongString(time, ['', '', '']),
    day: (time) => formatToLongString(time, ['', '', '']),
  },
}
